<template>
  <div class="cart bg-primary">
    <div class="search-frame fixed-top px-2 py-4 searchgbar bg-secondary d-flex justify-content-between">
      <img src="../assets/images/profileIcon.png" alt="" height="30px">
      <p class="mb-0 h5 text-white d-flex align-items-center">我的購物車</p>
      <img src="../assets/images/backhome.png" alt="" height="30px" @click="$router.push({ name: 'Store' })">
    </div>
    <div class="cart-list">
      <div class="p-2">
        <div v-for="(item,index) in cartlist" :key="index" class="cart-list-item d-flex p-1 bg-white mb-2">
          <img :src="item.img" alt="" class="w-25 h-25 img-fluid mr-2" height="">
          <div class="w-50 position-relative">
            <p class="mb-0 text-left">{{ item.name }}</p>
            <div class="numbercal d-flex border border-black w-50 position-absolute mb-0 h-25 justify-content-around">
              <div @click="handleSub(item)">-</div>
              <div>{{ item.count }}</div>
              <div @click="handlePlus(item)">+</div>
            </div>
          </div>
          <p v-if="item.discount" class="d-flex align-items-center  text-truncate justify-content-center mb-0 w-25 text-right text-danger float-right"> ${{ item.discount | currency }} </p>
          <p v-else class="d-flex align-items-center  text-truncate justify-content-center mb-0 w-25 text-right text-danger float-right"> ${{ item.price | currency }} </p>
          <div class="bg-danger text-white d-flex align-items-center del-btn" @click="del(index)">移除</div>
        </div>
        <div v-if="!cartlist || cartlist.length === 0">
          <p>您的購物車目前是空的</p>
          <u>
            <router-link class="text-secondary" to="/store">去逛逛吧</router-link>
            <router-view/>
          </u>
        </div>
      </div>
    </div>
    <div class="search-frame fixed-bottom shadow-lg bg-secondary d-flex justify-content-between">
      <p class="pl-2 text-white mb-0 h5 text-left align-items-center d-flex">總金額: $ {{ totalMoney | currency}}</p>
      <router-link class="text-secondary" to="/purchase"><div class="bg-white py-3 px-6 h5 mb-0 shadow-lg">結帳</div></router-link>
      <router-view/>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'Cart',
  data () {
    return {
      // productName: ''
      // productList: []
    }
  },
  // created () {
  //   this.productList = this.cartlist
  // },
  filters: {
    currency (price) {
      return price.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
    }
  },
  computed: {
    ...mapState({
      cartlist: state => state.cartlist
    }),
    totalMoney () {
      if (!this.cartlist) return 0
      const money = this.cartlist.map(item => {
        return item.count * (item.discount ? item.discount : item.price)
      })
      const total = money.reduce((val, cur) => {
        return val + cur
      }, 0)
      return total
    }
  },
  methods: {
    handleSub (item) {
      if (item.count === 1) return
      this.$store.commit('subCartList', item)
    },
    handlePlus (item) {
      this.$store.commit('addCartList', item)
    },
    del (index) {
      this.$store.commit('delCartList', index)
    }
  }
}
</script>
