<template>
  <div class="home container">
    <div class="row">
      <div class="col p-5">
        <template v-if="Boolean(this.google) && Boolean(this.map)">
          <slot
            :google="google"
            :map="map"
          />
        </template>
        <p>前高雄縣停車場資訊</p>
        <select v-model="selected">
        <option disabled value="">選擇區域</option>
        <option v-for="(option,index) in options" :key="index">
          {{ option }}
        </option>
      </select>
        <table class="table table-striped mt-3">
          <thead>
            <tr>
              <th scope="col" v-for="(item, key) in newData[0]" :key="key" v-show="key!== 'seq'">{{ key }}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(items, index) in newData" :key="index">
              <th scope="row" v-for="(item,index) in items" :key="index" v-show="index!== 'seq'">{{ item }}</th>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import axios from 'axios'
export default {
  name: 'Parking',
  components: {
    // HelloWorld
  },
  data () {
    return {
      options: [],
      selected: '',
      newData: []
    }
  },
  created () {
    this.getData()
  },
  watch: {
    selected () {
      this.changeArea()
    }
  },
  methods: {
    getData () {
      axios({
        method: 'get',
        url: 'https://api.kcg.gov.tw/api/service/Get/897e552a-2887-4f6f-a6ee-709f7fbe0ee3',
        responseType: 'json'
      }).then(res => {
        this.newData = res.data.data
        var newarray = []
        for (let i = 0; i < res.data.data.length; i++) {
          newarray.push(res.data.data[i].行政區)
          // res.data.data.retVal[i].sarea.push(result)
        }
        this.options = [...(new Set(newarray))]
      })
    },
    changeArea () {
      axios({
        method: 'get',
        url: `https://api.kcg.gov.tw/api/service/Get/897e552a-2887-4f6f-a6ee-709f7fbe0ee3?filters={"行政區": "${this.selected}"}`,
        responseType: 'json'
      }).then(res => {
        this.newData = res.data.data
      })
    }
  }
}
</script>
