<template>
  <div class="todolist">
    <div class="container">
      <div class="row">
        <div class="col p-3 m-3 rounded border border-secondary bg-light">
          <p class="h2 text-dark mb-3">待辦清單</p>
          <div class="todolist-add d-flex border-bottom align-items-center text-center px-3 justify-content-between">
            <input type="text" v-model="newlist" placeholder="輸入新增事項..." @keyup.enter="addList(newlist)">
            <div class="todolist-add-icon d-flex ml-2 justify-content-center align-items-center" @click="addList(newlist)">+</div>
          </div>
          <ul class="list-unstyled mt-3">
            <li class="rounded border border-gray d-flex p-3 mb-3 align-items-center justify-content-between" v-for="(item,index) in allList" :key="index">
              <div class="d-flex align-items-center">
                <input type="checkbox" :id="`${item}_${index}`" :value="index" v-model="select">
                <label :for="`${item}_${index}`" class="mb-0 ml-2" >{{ item }}</label>
              </div>
              <div class="todolist-del-icon d-flex ml-2 justify-content-center align-items-center" @click="del(index)">一</div>
              <!-- <img height="25px" src="../assets/css/sass/helper/trashcan.png" alt="" @click="del(index)"> -->
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      newlist: '',
      allList: [],
      select: []
    }
  },
  methods: {
    addList (item) {
      if (item === '') return
      this.allList.push(item)
      this.newlist = ''
    },
    del (index) {
      this.allList.splice(index, 1)
    }
  }
}
</script>
