  filters: {
    currency (price) {
      return price.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
    }
  },
  computed: {
    ...mapState({
      cartlist: state => state.cartlist
    }),
    searchproduct () {
      return this.productlist.filter(item => {
        return item.name.match(this.productName)
      }
      )
    },
    cartcount () {
      const num = this.cartlist.map(item => {
        return item.count
      }).reduce((val, cur) => {
        return val + cur
      }, 0)
      return num
    }
  },
  methods: {
    addToCart (item) {
      this.$store.commit('addCartList', item)
      window.alert('已加入購物車')
    }
  }
