    initWebSocket () { // 初始化weosocket
      console.log('init')
      this.connection = new WebSocket('wss://echo.websocket.org')
      this.connection.onmessage = this.onmessage
      this.connection.onopen = this.onopen
      this.connection.onerror = this.onerror
      this.connection.onclose = this.onclose
    },
    sendMessage () {
      console.log(this.connection)
      console.log('73', this.content)
      this.connection.send(this.content)
      this.record.push({ content: this.content, type: 0 })
      console.log(this.record)
      this.content = ''
    },
    onopen () {
      console.log('onopen')
    },
    onerror () {
      console.log('error')
    },
    onclose () {
      console.log('close')
    },
    onmessage (e) {
      console.log(e.data)
      this.record.push({ content: e.data, type: 1 })
      const contentView = document.querySelector('.content')
      this.$nextTick(() => {
        contentView.scrollTop = contentView.scrollHeight
      })
    }
  }
